.blog {
	.card {
		align-items: stretch;
		min-height: 360px;

		h3 {
			margin: 0;

			a {
				text-decoration: none;
			}
		}
	}

	.card__media {
		box-sizing: border-box;
		padding: 24px;
		display: flex;
		flex-grow: 1;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		flex-direction: row;
		align-items: flex-end;
		cursor: pointer;
	}

	.card__supporting-text {
		p:last-child {
			margin-bottom: 0;
		}
	}
}
